.skills-map {
  padding: 2rem;
}

.nodes {
  display: flex;
  flex-wrap: wrap;
  gap: 1rem;
}

.node {
  padding: 1rem;
  background: #222;
  border: 1px solid #555;
  border-radius: 8px;
  cursor: pointer;
  transition: background 0.2s;
}

.node:hover {
  background: #333;
}

.skill-detail {
  margin-top: 2rem;
  padding: 1rem;
  background: #1a1a1a;
  border: 1px solid #444;
  border-radius: 8px;
}
