body {
  margin: 0;
  font-family: 'Inter', sans-serif;
  background-color: #0d1117;
  color: #c9d1d9;
}

.App-header {
  padding: 1rem 2rem;
  background-color: #161b22;
  border-bottom: 1px solid #30363d;
  text-align: center;
}

.site-title {
  margin: 0;
  font-size: 2rem;
  color: #ffffff;
}

.nav-bar {
  margin-top: 1rem;
  display: flex;
  justify-content: center;
  gap: 2rem;
  flex-wrap: wrap;
}

.nav-link {
  color: #58a6ff;
  text-decoration: none;
  font-weight: 500;
  font-size: 1.1rem;
  padding: 0.3rem 0.8rem;
  border-radius: 6px;
  transition: background 0.2s ease;
}

.nav-link:hover {
  background-color: #21262d;
}

.nav-link.active {
  background-color: #238636;
  color: #fff;
}

.App-main {
  /* padding: 2rem; */
  max-width: 1000px;
  margin: 0 auto;
  overflow: visible;
}
